@import "https://fonts.googleapis.com/css2?family=Poppins&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background-color: #000;
  position: relative;
}

.header {
  width: 100%;
  padding: 20px 10px;
}

.navbar {
  width: 100%;
  color: #f5f5f5;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.nav-humburger img, .close-menu img {
  height: 30px;
}

.movie-display.show-menu {
  transform: translateX(0);
}

.overlay {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  z-index: 100;
  background-color: #00000080;
  top: 0;
  left: 0;
}

.movie-display {
  width: 60%;
  color: #f5f5f5;
  z-index: 100;
  background-color: #000;
  flex-direction: column;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: translateX(-100%);
}

.close-menu {
  width: 100%;
  padding: 20px;
}

.close-menu img {
  float: right;
}

.wrapper form {
  text-align: center;
  width: 100%;
  margin-top: 30px;
}

.wrapper form input {
  height: 40px;
  width: 60%;
  border-radius: 8px;
}

.pagination {
  width: 50%;
  flex-direction: row;
  justify-content: space-between;
  margin: 20px auto;
  display: flex;
}

.pagination button {
  background-color: #0ff;
  border-radius: 8px;
  padding: 10px 20px;
}

.search {
  margin-bottom: 10px;
  padding-left: 20px;
}

.individual-wrapper {
  width: 60%;
  margin: 30px auto;
}

.individual-mo {
  color: #f5f5f5;
  flex-direction: row;
  align-items: center;
  gap: 10%;
  margin-bottom: 30px;
  padding: 10px 20px;
  text-decoration: none;
  display: flex;
}

.individual-mo:hover {
  cursor: pointer;
  background-color: #5a5959;
  margin-left: 5px;
}

.movie-img {
  height: 50px;
  width: 50px;
}

.details-mo {
  width: 70%;
}

.movie-img img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.before-load-text {
  text-align: center;
  color: #f5f5f5;
}

.picture-div {
  width: 80%;
  margin: 20px auto;
}

.movie-picture {
  width: 100%;
  position: relative;
}

.movie-picture img {
  width: 100%;
  border-radius: 16px;
  box-shadow: -10px 10px 20px 10px #faf7f759;
}

.movie-picture div {
  width: 100%;
  text-align: center;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 5%;
}

.movie-picture div a {
  color: #000;
  background-color: #0ff;
  border-radius: 18px;
  padding: 20px 30px;
  font-size: 80%;
  text-decoration: none;
}

.movie-details {
  width: 90%;
  color: #f5f5f5;
  flex-direction: row;
  margin: 50px auto;
  padding-left: 2%;
  display: flex;
}

.details-header h2 {
  text-align: center;
  margin-top: 20px;
}

.details-header p {
  text-align: center;
  font-style: italic;
}

.storylinecast {
  width: 100%;
  flex-direction: row;
  margin-top: 100px;
  display: flex;
}

.storyline {
  width: 60%;
  padding: 10px;
}

.storyline h4 {
  margin-bottom: 40px;
}

.storyline p {
  font-style: italic;
  line-height: 30px;
}

.special-details {
  width: 40%;
  color: #f5f5f5;
  margin-left: 20px;
  padding: 40px 50px;
  box-shadow: -1px 1px 10px 2px #91909059;
}

.special-details a {
  color: #f5f5f5;
  text-decoration: none;
}

.special-details a:hover {
  color: #7fffd4;
}

.special-details h4 {
  margin-bottom: 40px;
}

.special-details p {
  margin-bottom: 15px;
}

footer {
  width: 100%;
  padding: 20px 20%;
}

footer p {
  text-align: center;
  color: #f5f5f5;
}

footer p a {
  color: #f5f5f5;
  text-decoration: none;
}

@media only screen and (max-width: 600px) {
  .movie-display, .individual-wrapper, .wrapper form input {
    width: 90%;
  }

  .movie-details {
    flex-direction: column-reverse;
    padding-left: 0;
    display: flex;
  }

  .storylinecast {
    width: 100%;
    flex-direction: column-reverse;
    padding-right: 0;
    display: flex;
  }

  .storyline {
    width: 100%;
    padding: 50px 0;
  }

  .special-details {
    width: 80%;
    margin: 20px auto;
  }
}

